[data-src] {
  // opacity: 0;
}


[data-fadeIn] {
  opacity: 0 !important;
  will-change: transform, opacity;


  &.fadeStart {
    transition: opacity 500ms !important;
    opacity: 1 !important;
  }
}


// Delayed media are always hidden
[data-delay]:not([data-fadeIn]) {
  display: none !important;
}


// Without javascript, dynamic nodes are hidden
.no-js [data-src] {
  display: none !important;
}
